*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Roboto", Arial, sans-serif;
  color: #121212;
  background-color: #E5E5E5;
}

.modal {
  background-color: rgba(18, 18, 18, 0.5);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  padding: 5.6rem 9.5rem;
  background-color: #FFFFFF;
}
.modal__heading {
  font-size: 4.2rem;
  font-weight: 700;
}
.modal__text {
  font-size: 1.6rem;
}
.modal__exit-icon {
  position: absolute;
  top: 4.6rem;
  right: 4.6rem;
  font-size: 1.2rem;
  cursor: pointer;
}
.modal__clicked-info {
  font-weight: 700;
}
.modal__hidden {
  display: none;
}

.button {
  background-color: #E4C1AE;
  padding: 1.6rem 4rem;
  border: none;
  border-radius: 5rem;
  font-size: 1.6rem;
  font-weight: 700;
  color: #FFFFFF;
  cursor: pointer;
}
.button__hidden {
  display: none;
}
.button--reset {
  width: min-content;
  margin-left: auto;
}

.content {
  max-width: 144rem;
  margin: 0 auto;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media only screen and (min-width: 28.125em) {
  .content {
    padding: 2.5rem 4rem;
  }
}
@media only screen and (min-width: 39.375em) {
  .content {
    padding: 3rem 5rem;
  }
}
@media only screen and (min-width: 48.125em) {
  .content {
    flex-direction: row;
    gap: 2rem;
    padding: 2rem;
  }
}
@media only screen and (min-width: 62.5em) {
  .content {
    padding: 4rem;
  }
}
@media only screen and (min-width: 75em) {
  .content {
    padding: 7.5rem;
  }
}
@media only screen and (min-width: 87.5em) {
  .content {
    padding: 9.6rem 23rem;
    gap: 9.5rem;
  }
}
.content__img-container {
  width: 100%;
  overflow: hidden;
  text-align: center;
}
@media only screen and (min-width: 48.125em) {
  .content__img {
    width: 40rem;
  }
}
@media only screen and (min-width: 62.5em) {
  .content__img {
    width: 50.5rem;
  }
}
.content__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media only screen and (min-width: 48.125em) {
  .content__wrapper {
    width: 80%;
    align-items: flex-start;
  }
}
.content__heading {
  font-size: 4.2rem;
  margin-top: 1rem;
}
@media only screen and (min-width: 48.125em) {
  .content__heading {
    margin-top: 0;
  }
}
.content__text {
  margin-top: 2.4rem;
  margin-bottom: 3.2rem;
  font-size: 1.6rem;
  font-weight: 300;
  text-align: justify;
}